  .page__map{
  padding-top: 250px;
  }
 .map{
  width: 70%;
  margin: 0 auto;
  & h2{
    font-family: 'Open Sans';
    font-size: 30px;
    font-weight: 600;
    color:#1f1f1f;
    margin: 0 auto 0 auto;
    padding: 0;
    text-align: center;
  }
  & p{
    max-width: 60%;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 17px;
    line-height: 31px;
    letter-spacing: 2px;
    text-align: center;
    color:#1f1f1f;
    margin: 20px auto 0 auto;
    padding: 0;
  }
  & img{
    display: block;
    width: 957px;
    height: 530px;
     margin: 43px auto 0 auto;
  }
  & ul{
    width: 50%;
    margin: 20px auto 0 auto;
    padding-left: 0;
    display: flex;
    justify-content: space-between;
    list-style: none;
  }

  & a{
    display: flex;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 16px;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding:15px 15px 15px 0;
  }
  & a[href*="mailto"]::before{
    content: "";
    width: 41px;
    height: 22px;
    background: url("../img/mail.svg") no-repeat;
  }
  & a[href*="tel"]::before{
    content: "";
    width: 37px;
    height: 23px;
    background: url("../img/icon.svg") no-repeat;
  }
 }

@media (max-width: 1023px) {
  .page__map{
    margin: 0 auto;
    margin-top: 150px;
   }
  .map{
    & h2{
      font-size: 24px;
    }
  }
}
 @media (max-width: 767px) {
    .map{
      & h2{
        font-size: 18px;
      }
      & ul{
        flex-direction: column;
       align-items:center;
      }
      & li {
        width: auto;
      }
    }
  }
