.header__wrapper{
  margin: 0 auto;
 }
 .wrapper{
   width: 61%;
 }
 .navigation{
   padding-left: 0;
  ul{
      display: flex;
      align-items:center;
      justify-content: space-between;
      flex-wrap: wrap;
      min-height: 52px;
      border-bottom:2px solid #ffffff;
      margin-top: 10px;
      padding: 0 25px 0 0;

    & li{
      list-style: none;

    }
    & a{
      font-family: 'Open Sans';
      font-weight: 300;
      line-height: 25px;
      font-size: 16px;
      text-decoration: none;
      color:#ffffff;
      margin: 0;
      }
      & a:focus,
      a:visited{
      opacity: 0.8;
      }
      & a:active{
      opacity: 0.3;
      }
   }
  }
   .button{
    display: none;
  }
  @media (max-width: 1023px) {
    .wrapper{
      width: 100%;
    }
  .navigation{
     width: 320px;
      min-height: 60px;
      margin: 0 auto;
      background-color: #e5e5e5;
      border-bottom:none;
      position: relative;
       & ul{
         flex-direction: column;
         justify-content:space-between;
         align-items:center;
         background-color: #e5e5e5;
         height: 330;
         z-index: 1;
         top: 51px;
         margin: 0 auto;
         padding:100px 0 440px 0;
        }
        & li{
         height:51px;
         background-color: #f6f6f6;
         width: 100%;
         margin-top: 20px;
         color: #48494D;
        }
        & li::after {
          position: absolute;
          content: "";
          top: 15px;
          right: 20px;
          display: inline-block;
          width: 25px;
          height: 25px;
          background: url("../img/arrow down.png") no-repeat;
        }
       }
  .navigation li a{
    font-family: 'Open Sans';
    font-weight: 400;
    line-height: 20px;
    color: #48494D;
    text-align: left;
    padding-left: 17px;
  }
    /*подключаются при включенном JS файле*/
  .navigation--js{
    width:100%;
    height: 50px;
    background-color:transparent;
   }
    .logo--js{
      position: absolute;
      top:10px;
      left: 10px;
    }
.navigation__button{
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
      display:block;
      width: 70px;
      height: 50px;
      background-color: #0AD9C6;
      cursor: pointer;
      border: none;

    &:hover,
    &:active {
      background-color: #0ac4b1;
    }
  }

  .header__nav--closed .navigation__button::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  }
  /*подключаются при нажатии на кнопку*/
  .header-nav--opened .navigation__button {
    top: 0;
    right: 0;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 24px;
      left: 18px;
      width: 36px;
      height: 2px;
      background-color: #ffffff;
    }

    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }

    &::after {
      transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
      background-color: rgba(255, 255, 255, 0.3);
    }
  }
    .close{
      display: none;
    }
  }
  @media (max-width: 767px) {
    .wrapper{
      width: 100%;
    }
  }
