.about{
  width: 100%;
  background-color:#e5e5e5;
  padding-top: 140px;
  padding-bottom: 3px;
}
.about__content{
  width: 85%;
  text-align: center;
  margin: 0 auto;
}
.about{
  h2{
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: 30px;
    font-style: normal;
    text-align: center;
    margin: 0;
  }
  & ul{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    list-style: none;
    margin: 5px auto 150px auto;
    padding-left: 0px;

  }
  & li{
    position: relative;
    counter-increment: num;
    width:24%;
    box-sizing:border-box;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 22px;
    line-height: 34px;
    font-style: normal;
    text-decoration: none;
    text-align: center;
    margin: 100px 0 0 0;

  }
  & li::before{
    position: absolute;
    content: "0" counter(num);
    height: 25px;
    width: 25px;
    left:50%;
    top: -55px;
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: 28px;
    color:#0ad9c6;
  }
}

@media (max-width: 1023px) {
  .about{
    padding-top: 160px;
    & ul{
      flex-wrap: wrap;
      justify-content: space-evenly;
    }
    & li{
      width:50%;
      padding: 0 110px;
      box-sizing:border-box;
    }

  }
}

@media (max-width: 767px) {
   .about{
    & ul{
      flex-direction:column;
      align-items: center;
    }
    & li{
      width: 100%;
    }
  }
}
